.toolbar-container {
  width: 90%;
}

.dropdown {
     position: relative;
     display: inline-block;
     background-color: #d03e3e;
     width: 200px;
     border-radius:5px;
}
 ul {
     list-style-type: none;
     margin: 0;
     padding: 0;
     top:45px;
     right:0px;
     width: 200px;
     background-color: white;
     font-weight:bold;
     position: absolute;

     box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
     z-index: 1;

     max-height: 300px;
     overflow-y: scroll;
}
 li {
     color: #000;
     text-decoration: none;
     padding: 8px 16px;
     border-bottom: 1px solid #e5e5e5;
}
 li:last-child {
     border-bottom: none;
}
 li:hover {
   background-color: #ef731a;
   color: white;
   cursor: pointer;
}
 .button{
     width:178px;
     background-color: #ef731a;
     padding:12px;
     border-radius:5px;
     font-weight:bold;
     color:white;
     display: inline-block;
}
.button:hover {
  cursor: pointer;
}
 .button:before{
     content:"";
     position:absolute;
     width:0px;
     height:0px;
     border: 10px solid;
     border-color: white transparent transparent transparent;
     right:15px;
     top:20px;
}
  .toggle:before{
    display: none;
  }
  .toggle {
    float: right;
  }
